//vuex
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

//
Vue.use(Vuex);

//store
const store = new Vuex.Store({
    //
    state: {
        //
        topics: [
            { name: 'all', description: '' },
            { name: 'good', description: '' },
            { name: 'share', description: '' },
            { name: 'ask', description: '' },
            { name: 'job', description: '' },
            { name: 'dev', description: '' }
        ],
        //
        showMenus: false,
        //
        isLogined: false,
        //
        loginname: '',
        //accesstoken
        accesstoken: '',
        //id
        author_id: '',
        // 
        unLoginedmoreMenus: {
            login: ''
        },
        loginedMoreMenus: {
            create: '',
            center: '',
            msg: '',
            logout: ''
        }
    },
    //
    getters: {
        moreMenus: function (state) {
            if (state.isLogined == true) {
                //
                return state.loginedMoreMenus;
            }
            return state.unLoginedmoreMenus;
        },
        getTopicDescByTab: function(state){
            //
            return function(tab){
                for(var index = 0; index < state.topics.length; index ++ ){
                    if(state.topics[index].name == tab){
                        return state.topics[index].description;
                    }
                }
            }
        }
    },
    mutations,
    actions
});

//
export default store;


